const fetch_all_rto = (e) => {
  console.log("eeeeeeeeeeeeeeeeeeeeeeeeeeee",e);
  if (e == undefined || e == '') {
      var ww = e
  } else {
      var ww = e.target.value
  }
  const params = {
    rto_no: ww
  };
  console.log("params", params);
  const res = fetch(api_url+"fetch_all_rto", {
      method: 'POST',
      body: JSON.stringify(params),
      headers: {
          "Content-type": "application/json; charset=UTF-8",
      }
  })
      .then((response) => response.json())
      .then(json => {
          var item_data = json;
          console.log("RTO response typeahead", json);
          if (item_data.status == true) {
              var kkk = item_data.data;
              var item_Object = kkk.map(item => {
                  return {
                      "label": item.city_name+" " + item.rto_no,
                      "value": item._id,
                  }

              });
              setSuggestions(item_Object)
          } else {
            var no_data={
              "label": "No Data",
              "value": "1",
            }
            setSuggestions([no_data])
          }
      })
}


